<template>
  <div class="box">
    <h1></h1>
    <div>
      <div>
        <span class="cre-inf displayNone">()</span>
        <textarea class="cre displayNone" name="cre" placeholder="123456789"></textarea>
        <span class="prk-inf displayNone">()</span>
        <textarea class="prk displayNone" name="cre" placeholder="123456789"></textarea>
      </div>
      
      <button class="submit1" @click="generateDAC()"></button>
    </div>
    <div>
      <button class="transfer displayNone" @click="toLoginIndex()"></button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
import Web3 from 'web3'
export default {
  name:'IN-Transfer',
  data() {
    return {
      
    }
  },
  methods: {
    generateDAC() {
      axios({
        url: '/api/in/credit',
        method: 'post',
        data: {
          cre1: sessionStorage.getItem('cre1'),
          cre2: sessionStorage.getItem('cre2'),
          username: sessionStorage.getItem('username'),
          password: sessionStorage.getItem('password'),
          email: sessionStorage.getItem('email'),
          attr1: sessionStorage.getItem('attr1'),
          attr2: sessionStorage.getItem('attr2'),
          attr3: sessionStorage.getItem('attr3')
        }
      }).then(result => {
        if(result.data.code == 0) {
          this.$message.error(result.data.msg)
        }
        else {
          document.querySelector('.submit1').classList.add('displayNone')
          document.querySelector('.cre-inf').classList.remove('displayNone')
          document.querySelector('.prk-inf').classList.remove('displayNone')
          document.querySelector('.transfer').classList.remove('displayNone')

          const prk = document.querySelector('.prk')
          const cre = document.querySelector('.cre')
          prk.classList.remove('displayNone')
          cre.classList.remove('displayNone')
          cre.value = result.data.data.cre
          prk.value = sessionStorage.getItem('prk')
          
          try{
            this.uploadBlock({
            type: result.data.data.tx,
            para1: result.data.data.para1,
            para2: result.data.data.para2,
            ts: result.data.data.ts,
            sign: result.data.data.sign
          })
          }catch{
            this.$message.error('')
          }
        }
      }).catch(() => {
        this.$message.error('')
      })
        
    }, 
    toLoginIndex() {
      this.$router.push('/')
    },
    
    uploadBlock(data) {
      const web3 = new Web3(new Web3.providers.HttpProvider("HTTP://127.0.0.1:7545"))
      web3.eth.sendTransaction({
        from: '0x24F006B75F942466DCFD24Ba80a5E53744774B4C',//fromthis.web3.eth.defaultAccount
        to: '0xc2cC927D5865e02c3aDBc86687f2c2fFbEBfc687',
        value: 100000000000000, //0.01 ether
        gas: 121000, //
        data: this.stringtoHex(JSON.stringify(data))
      }).then(function (receipt) {
        //receipt
        console.log("", receipt.status)
      })
    },

    stringtoHex(str) {
      var val = "0x";
      for (var i = 0; i < str.length; i++) {
          if (val == "")
              val = str.charCodeAt(i).toString(16);
          else
              val += str.charCodeAt(i).toString(16);
      }
      console.log(val)
      return val
    }
  }
}
</script>

