<template>
  <div>
    <div class="corp-title-text">
      <div class="themis corp-title-text2"><strong></strong></div>
    </div>
    <div class="user-inf">
      <div class="user-title"></div>
      <div class="dp-case">
        <span class="name"></span>
        <span class="number">{{ dpSize }}</span>
      </div>
      <div class="in-case">
        <span class="name"></span>
        <span class="number">{{ inSize }}</span>
      </div>
      <div class="crime-case">
        <span class="name"></span>
        <span class="number">{{ dataSize }}</span>
      </div>
    </div>

    <div class="crime-inf">
      <div class="myChart"><canvas id="myChart" width="400" height="400"></canvas></div>
      <div class="chartName"></div>
    </div>
    
    <div class="block-inf">
      <div class="block-title"></div>
      <div class="box2"></div>
    </div>

    <div class="trace">
      <el-upload
        class="upload-demo"
        :multiple="false"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary"></el-button>
        <span style="display:inline-block; width: 50px;"></span>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="uploadImage()"></el-button>
      </el-upload>
    </div>

    <div class="trace-res"></div>


  </div>
</template>

<script>
import axios from 'axios'
import Chart from 'chart.js/auto'
import Web3 from 'web3'

export default {
  name:'IndexView',
  data() {
    return {
      fileList: [],
      dpSize: 0,
      inSize: 0,
      dataSize: 0
    }
  },
  methods: {
    hextoString(str) {
      var out = "", len = str.length/2;
      for(var i = 0; i < len; i++){
          out += String.fromCharCode(parseInt(str.substr(i*2,2),16));  
        }
        // 
      return this.utf8to16(out)
    },
    utf8to16(str) {
      var out, i, len, c;
      var char2, char3;
      out = "";
      len = str.length;
      i = 0;
      while(i < len) {
        c = str.charCodeAt(i++);
        switch(c >> 4){ 
          case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
                out += str.charAt(i-1);
          break;
          case 12: case 13:
              char2 = str.charCodeAt(i++);
              out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
          break;
          case 14:
                char2 = str.charCodeAt(i++);
                char3 = str.charCodeAt(i++);
                out += String.fromCharCode(((c & 0x0F) << 12) |
                ((char2 & 0x3F) << 6) |
                ((char3 & 0x3F) << 0));
            break;
          }
      }
      return out
    },
    uploadImage() {
      const files = document.querySelector('.el-upload__input').files
      if(files.length == 0) {
        this.$message.error('')
        return
      }
      const form = new FormData()
      form.append("file", files[0])
      axios({
        url: '/api2/imOutFile',
        method: 'post',
        headers: {
          token: sessionStorage.getItem('token'),
          "Content-Type": 'multipart/form-data'
        },
        data: form
      }).then(result => {
        console.log(result.data)
        if(result.data.code == 0) {
          this.$message.error('')
        } else {
          document.querySelector('.trace-res').innerHTML = result.data.data
          this.$message.success(result.data.msg)
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  },
  mounted() {
    axios({
      method: 'get',
      url: '/api/manage',
      headers: {
          token: sessionStorage.getItem('token')
        }
    }).then(result => {
      this.dpSize = Number(result.data.data.dpSize)
      this.inSize = Number(result.data.data.inSize)
      this.dataSize = Number(result.data.data.dataSize)

      const murderNum = Number(result.data.data.murder)
      const robNum = Number(result.data.data.rob)
      const rapeNum = Number(result.data.data.rape)
      const stealNum = Number(result.data.data.steal)
      
      const code = result.data.code
      if(code == 0) {
        this.$message.error(result.data.msg)
      }
      else if(code == 1) {
        Chart.defaults.color = '#FFF'
        const ctx = document.getElementById('myChart');
        const data = {
          labels: [
            '',
            '',
            '',
            ''
          ],
          datasets: [{
            label: '',
            data: [murderNum, robNum, rapeNum, stealNum],
            backgroundColor: [
              'rgb(255, 99, 132)',
              'rgb(54, 162, 235)',
              'rgb(255, 205, 86)',
              'rgb(42, 170, 31)'
            ],
            hoverOffset: 4
          }]
        };
        const config = {
          type: 'pie',
          data: data,
          options: {
            responsive: true, // 
            maintainAspectRatio: false,// 
            scales: {
              yAxes: [{
                ticks: {
                  beginAtZero:true
                }
              }]
            },
            plugins: {
              legend: {
                  labels: {
                      font: {
                          size: 16
                          
                      }
                  }
              }
          }
          }
        };
        const myChart = new Chart(ctx, config);
        myChart.data
      }
    }).catch(() => {
      console.log('')
    })

    const web3 = new Web3(new Web3.providers.HttpProvider("HTTP://127.0.0.1:7545"))
    web3.eth.getBlockNumber().then(index => {
      index = Number(index)
      while (index >= 1) {
        web3.eth.getBlock(index).then(block => {
          if (block.transactions === undefined) return

          console.log(block.gasUsed)
          const transactions = block.transactions
          console.log(transactions)
          transactions.forEach(element => {
            web3.eth.getTransaction(element).then(transaction => {
              if (transaction.data !== '0x') {
                const hexData = transaction.data
                const jsonData = this.hextoString(hexData.substring(2, hexData.length))
                try{
                  const data = JSON.parse(jsonData)
                  document.querySelector('.box2').innerHTML += `<div class="historyTr">
                  <div class="head" style="border: 1px solid red; border-radius: 5px;"><span style="position: absolute; left:-84px;">:</span>${block.hash}</div>
                  <div class="body">
                    <div style="border: 1px solid red; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-84px;">:</span>${data.type}</div>
                    <div style="border: 1px solid red; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-57px;">1:</span>${data.para1}</div>
                    <div style="border: 1px solid red; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-57px;">2:</span>${data.para2}</div>
                    <div style="border: 1px solid red; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-47px;">:</span>${data.sign}</div>
                  </div>
                  <div class="trDate" style="margin-bottom: 20px;">: ${data.ts}</div>
                </div>`
                }catch{
                  console.log(111)
                }
              }
            })
          })
        })
        index--
      }
    })

  }
  }
</script>

