<template>
  <div class="box">
    <h1></h1>
    <div>
      <div>
        <span class="cre-inf displayNone">()</span>
        <textarea class="cre displayNone" name="cre" placeholder="123456789"></textarea>

      </div>
      
      <button class="submit1" @click="generateDAC()"></button>
    </div>
    <div>
      <button class="transfer displayNone" @click="toLoginIndex()"></button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name:'DP-Transfer',
  data() {
    return {
      
    }
  },
  methods: {
    generateDAC() {
      axios({
        url: '/api/dp/credit',
        method: 'post',
        data: {
          cre1: sessionStorage.getItem('cre1'),
          cre2: sessionStorage.getItem('cre2'),
          username: sessionStorage.getItem('username'),
          password: sessionStorage.getItem('password'),
          email: sessionStorage.getItem('email'),
        }
      }).then(result => {
        if(result.data.code == 0) {
          this.$message.error(result.data.msg)
        }
        else {
          document.querySelector('.submit1').classList.add('displayNone')
          document.querySelector('.cre-inf').classList.remove('displayNone')
          document.querySelector('.transfer').classList.remove('displayNone')

          const cre = document.querySelector('.cre')
          cre.classList.remove('displayNone')
          cre.value = result.data.data.cre
        }
      }).catch(() => {
        this.$message.error('')
      })
        
    }, 
    toLoginIndex() {
      this.$router.push('/')
    },
  }
}
</script>

