<template>
  <div>
    <div class="corp-title-text">
      <div class="themis corp-title-text2"><strong></strong></div>
    </div>
    <div class="box">
    <h1></h1>
    <div>
      <input class="username" type="text" placeholder="">
      <br>
      <input class="password" type="password" placeholder="">
      <button @click="login" class="submit"></button>
    </div>
    </div>
  </div>  
</template>

<script>
import axios from 'axios'
export default {
  name:'LoginView',
  data() {
    return {

    }
  },
  methods: {
    login(){
      const username = document.querySelector('.username').value
      const password = document.querySelector('.password').value
      axios({
        method: 'post',
        url:'/api/admin/login',
        data: {
          username: username,
          password: password
        }
      }).then(result => {
        const code = result.data.code
        if(code == 0) {
          this.$message.error(result.data.msg)
        }
        else if(code == 1) {
          sessionStorage.setItem('token', result.data.data.token)
          this.$router.push('/admin/index')
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
        }
      }).catch(() => {
        this.$message.error('')
      })
    }
  }
}
</script>

<style>
body {
  background: url('../../images/admin-login-background-image.jpg') repeat;
  background-size: cover;
}

.el-message__content {
  font-size: 30px;
}
</style>

